import React, { useState } from 'react';
import styled from 'styled-components';
import { MdVerifiedUser, MdFlag } from 'react-icons/md';
import { useAlertState } from '@components/libs/Notify/useAlertState';
import Alert from '@components/libs/Alert';
import SojiBaseInit from '@modules/SojiBase';
import Modal from '@modules/Modal';
import AddToVerify from './AddToVerify';

const Title = styled.p`
    font-weight: bold;
`;

const Button = styled.button`
    padding: 10px;
    border-radius: 5px;
    box-shadow: rgb(0 0 0 / 5%) 0px 0px 20px;
    width: 100%;
    margin-top: 5px;
    margin-bottom: 5px;
    background: ${({ red }) => (red ? 'rgb(255, 0, 0, 0.14)' : 'rgba(11, 151, 58, 0.14)')};
`;

const Container = styled.div`
    width: 100%;
    display: flex;
`;
const IconBox = styled.div`
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
`;
const ActionCardWrapper = styled.div`
    flex: 1;
    text-align: left;
    margin-left: 10px;
`;
const ActionCardTitle = styled.div`
    font-weight: 600;
    color: ${({ red }) => (red ? 'red' : 'rgb(11, 151, 58)')};
`;
const ActionCardDesc = styled.div`
    color: ${({ red }) => (red ? 'red' : 'rgb(11, 151, 58)')};
`;

export default function Disclaimer({ result, contentType, content }) {
    const alertState = useAlertState();
    const [state, setState] = useState({
        openFlagModal: false,
        openVerifyModal: false,
        buttonLoading: false,
        openVerifyBoxModal: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const WarningDescription = ({ searchedContent }) => {
        return (
            <div>
                Are you sure you want to flag <b style={{ color: 'red' }}>{searchedContent}</b> as a suspicious{' '}
                {contentType ? 'Phone Number' : 'Email'}?
            </div>
        );
    };

    const flagAction = async () => {
        stateHandler('buttonLoading', true);
        const SojiBase = await SojiBaseInit;
        const payload = { content };
        const getter = await SojiBase.post(
            `v1/search_engine/${contentType == 'Phone Number' ? 'flag_phone_numer' : 'flag_email'}`,
            payload,
        );
        getter.on('static', async (response) => {
            stateHandler('buttonLoading', false);
            stateHandler('openFlagModal', false);
            alertState.set({
                state: true,
                title: 'Notification',
                desc: response.response.data,
                type: 'success',
            });
        });
    };

    return (
        <>
            <Title>{result.description}</Title>

            <Button red onClick={() => stateHandler('openFlagModal', true)}>
                <Container>
                    <IconBox>
                        <MdFlag size={24} color="red" />
                    </IconBox>
                    <ActionCardWrapper>
                        <ActionCardTitle red>Flag</ActionCardTitle>
                        <ActionCardDesc red>
                            Flag {result.searchedContent} as a suspicious {contentType ? 'Phone Number' : 'Email'}
                        </ActionCardDesc>
                    </ActionCardWrapper>
                </Container>
            </Button>

            <Button onClick={() => stateHandler('openVerifyBoxModal', true)}>
                <Container>
                    <IconBox>
                        <MdVerifiedUser size={24} color="rgb(11, 151, 58)" />
                    </IconBox>
                    <ActionCardWrapper>
                        <ActionCardTitle>Verify</ActionCardTitle>
                        <ActionCardDesc>
                            Confirm {result.searchedContent} as a verified {contentType ? 'Phone Number' : 'Email'}
                        </ActionCardDesc>
                    </ActionCardWrapper>
                </Container>
            </Button>

            <Modal visible={state.openVerifyBoxModal} onRequestClose={() => stateHandler('openVerifyBoxModal', false)}>
                <AddToVerify
                    searchedContent={result.searchedContent}
                    onRequestClose={() => stateHandler('openVerifyBoxModal', false)}
                    contentType={contentType}
                />
            </Modal>

            <Alert
                visible={state.openFlagModal}
                setModal={() => stateHandler('openFlagModal', false)}
                actionDescription={<WarningDescription searchedContent={result.searchedContent} />}
                onActionButtonClick={() => flagAction()}
                buttonLoading={state.buttonLoading}
                actionButtonText="Okay, Add"
                backgroundColor="red"
            />
        </>
    );
}
